.section__padding {
  padding: 4rem 6rem;
}

.flex__center {
  display: flex;
  justify-content: center;
  align-items: center;
}

.app__bg {
  background: url(./assets/bg.png);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
}

.app__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 100vh;
}

.app__wrapper_info {
  flex: 1;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
}

.app__wrapper_img {
  flex: 1;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.app__wrapper_img-reverse {
  justify-content: flex-start;
  margin-right: 2rem;
}

.app__wrapper_img img {
  width: 350px;
}

.customBtn {
  background-color: var(--color-golden);
  color: var(--color-black);
  font-family: var(--font-base);
  font-weight: 700;
  letter-spacing: 0.04em;
  line-height: 28px;
  font-size: 16px;
  padding: 0.5rem 1.5rem;
  border-radius: 1px;
  border: none;
  outline: none;
  cursor: pointer;
}

.cormorant {
  font-family: var(--font-base);
  color: var(--color-white);
  font-weight: 700;
  letter-spacing: 0.04em;
  text-transform: capitalize;
  font-feature-settings: "tnum" on, "lnum" on;
  line-height: 29.9px;
  font-size: 23px;
}

.opensans {
  font-family: var(--font-alt);
  color: var(--color-white);
  font-weight: 400;
  letter-spacing: 0.04em;
  text-transform: capitalize;
  line-height: 28px;
  font-size: 16px;
}

.headtext__cormorant {
  font-family: var(--font-base);
  color: var(--color-golden);
  font-size: 64px;
  line-height: 83.2px;
  letter-spacing: 0.04em;
  text-transform: capitalize;
}

.spoon__img {
  width: 45px;
}

@media screen and (min-width: 2000px) {
  .customBtn,
  .cormorant {
    font-size: 37px;
    line-height: 67px;
  }

  .opensans {
    font-size: 30px;
    line-height: 50px;
  }

  .headtext__cormorant {
    font-size: 150px;
    line-height: 210px;
  }

  .spoon__img {
    width: 80px;
  }
}

@media screen and (max-width: 1150px) {
  .wrapper {
    flex-direction: column;
  }

  .wrapper-img {
    margin: 5rem 0 0 0;
  }

  .wrapper_img-reverse {
    margin: 0 0 5rem 0;
  }

  .wrapper-img img {
    width: 100%;
  }
}

@media screen and (max-width: 872px) {
  .section__padding {
    padding: 4rem;
  }
  .app__wrapper_img {
    margin-left: 2rem;
    margin-bottom: 30px;
  }
  .app__wrapper_img img {
    width: 400px;
  }
}

@media screen and (max-width: 650px) {
  .section__padding {
    padding: 3rem 2rem;
  }

  .cormorant {
    font-size: 21px;
  }

  .opensans {
    font-size: 14px;
  }
}

@media screen and (max-width: 480px) {
  .opensans {
    font-size: 12px;
  }

  .cormorant {
    font-size: 18px;
    line-height: 25px;
  }

  .headtext__cormorant {
    font-size: 45px;
    line-height: 70px;
  }
  .app__wrapper_img img {
    width: 80%;
  }
}
